<template>
	<div>
		<top></top>
		<MovieList title="影院热映" :imgdata='arr'></MovieList>
		<MovieList title="免费在线观影" :imgdata='arr'></MovieList>
		<MovieList title="新片速递" :imgdata='arr'></MovieList>
		<div class="content">
		<div class="box">
			<div class="box-t">
				<p>发现好电影</p>
				<span>更多</span>
			</div>
			<!-- <div class="box-b" v-for="(v,i) in list" :key="i">
				<a :href="v.href" class="text" :style="{'color':v.color}">{{v.title}}</a>
			</div> -->
			<div class="box-b">
				<a :href="v.href" class="text" v-for="(v,i) in list" :key="i" :style="{'color':v.color}">{{v.title}}</a>
			</div>
      	</div>
		</div>
	</div>
</template>

<script>
	import Top from '../components/top/top.vue'
	import MovieList from '../components/movielist/movielist'
	import axios from 'axios'
	export default{
		components:{
			Top,MovieList
		},
		data(){
			return{
				arr:[],
				list:[]
			}
		},
		mounted(){
			axios({
				url:'/data/movie',
				method:"get"
			}).then((ok)=>{
				// console.log(ok.data.movie);
                this.arr=ok.data.movie;
			}),
			axios({
				url:'/data/faxian',
				method:"get"
			}).then((ok)=>{
				// console.log(ok.data.faxianhaodianying);
				this.list=ok.data.faxianhaodianying;
			})
		}
	}
</script>

<style scoped>
.content {
  min-height: 0.5rem;
  margin: 0.58rem 0.48rem 0 0.48rem;
}
.box {
  width: 100%;
  min-height: 0.5rem;
}
.box-t {
  display: flex;
  justify-content: space-between;
}
.box-t p {
  font-size: 16px;
  line-height: 0.32rem;
  color: #111;
}
.box-t span {
  font-size: 14px;
  line-height: 0.3rem;
  color: #42bd56;
}
.box-b {
  min-height: 1rem;
  width: 100%;
  overflow: auto;
  overflow-y:hidden;
  display: flex;
  flex-direction: row;
}
.text{
	min-width:2rem;
	height:0.58rem;
	border:1px solid #ffac2d;
	margin:0.1rem 0.15rem;
	text-align: center;
	line-height: 0.3rem;
	font-size:0.16rem;
}
</style>
