<template>
	<div>
		<top></top>                            
		<div class="content">   
			<div class="box" v-for="(v,i) in 4" :key="i">
				<div class="box-t">
				<p></p>
				<span>更多</span>
				</div>
				<div class="box-b">
				<div class="con" v-for="(v,i) in arr" :key="i.id">
					<img :src="v.image" alt="" />
					<div class="name">{{v.title}}</div>
					<span class="xing">{{v.year}}</span>
				</div>
				</div>
			</div>

			<div class="box">
			<div class="box-t">
				<p>发现好书</p>
				<span>更多</span>
			</div>
			<div class="box-b">
				<a :href="v.href" class="text" v-for="(v,i) in list" :key="i" :style="{'color':v.color}">{{v.title}}</a>
			</div>
			
      		</div>
    	</div>
	</div>
</template>

<script>
	import Top from '../components/top/top.vue'
	import axios from 'axios'
	export default{
		components:{
			Top
		},
		data(){
			return{
				arr:[],
				list:[]
			}
		},
		mounted(){
			axios({
				url:'/data/books',
				method:"get"
			}).then((ok)=>{
				// console.log(ok.data.books);
                this.arr=ok.data.books;
			}),
			axios({
				url:'/data/goodbooks',
				method:"get"
			}).then((ok)=>{
				// console.log(ok.data.goodBook);
                this.list=ok.data.goodBook;
			})
		}
	}
</script>

