<template>
	<div>
		<div class="header">
			<div @click="$router.back()" class="off">取消</div>
			<div class="tit">
				 注册豆瓣
			</div>
		</div>

		
		<div class="login">
			<input autocomplete="off" type="text" name="u" label='用户' placeholder="账号：" required="required" v-model="username" />
			<input autocomplete="off" type="password" name="p" label='密码' placeholder="密码：" required="required" v-model="password" />
			<button type="submit" class="btn btn-primary btn-block btn-large" @click="reg">注册</button>
			{{errorMess}}
		</div> 
	</div>
	
</template> 

<script>
import axios from 'axios'
	export default{
		data(){
			return{
				username:'',
				password:'',
				errorMess:''
			}
		},
		methods:{
			reg(){
				axios({
					url:'/api/reg',
					method:'post',
					data:{
						username:this.username,
						password:this.password
					}
				}).then(
					res=>{
						if(res.data.err===0){
							this.$router.replace('/login')
						}else{
							this.errorMess = res.data.msg
						}
					}
					
				)
			}
		}
	}
</script>

