			}
		},
		components:{
			Top
		},
		// created(){
		// 	var newId=this.$router.query._id
		// }
		mounted() {
			axios({
				url: "/data/home",
				method: "get"
			}).then((ok) => {
				// console.log(this.$route.params.id)
				let id = this.$route.params.id;
				let data = ok.data.shouye.find(function(obj) {
					return obj.id == id
				})
				this.detail = data
				// console.log(this.detail.title)

			})
		}
	}
</script>

