<template>
	<div>
		<Top></Top>
		<Button></Button>
		<div class="content">
			<List :obj="arr"></List>
		</div>
	</div>
</template>

<script>
	import Top from '../components/top/top.vue'
	import Button from '../components/button/button'
	import List from '../components/list/list'
	import axios from 'axios'
	export default{
		components:{
			Top,Button,List
		},
		data(){
			return{
				arr:[]
			}
		},
		mounted(){
			axios({
				url:'/data/home',
				method:"get"
			}).then((ok)=>{
				// console.log(ok.data.shouye);
                this.arr=ok.data.shouye;
			})
		}
	}
</script>

<style scoped>
.content{
	background:#fff;
	margin:0 0.58rem;
}
</style>
